.overlay {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 10;
}

.modal {
	width: 400px;
	padding: 1rem;
	border-radius: 5px;
	background-color: #fff;
	box-shadow: 1px 2px 10px 1px rgba(0, 0, 0, 0.3);
}

.tit {
	margin-bottom: 5px;
	font-size: 1rem;
}

.note-area {
	width: 100%;
	padding: 10px;
}

.error-message {
	font-size: 0.875rem;
	color: red;
}

/* Palette */
.color-palette {
	display: flex;
	align-items: center;
	margin-bottom: 10px;
}

.color-palette div {
	position: relative;
	width: 27px;
	height: 27px;
	border-radius: 50%;
	cursor: pointer;
	margin-right: 5px;
}

.color-palette div:last-child {
	margin-right: 0;
}

.check-mark {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: white;
	font-size: 18px;
}
